import seedrandom from 'seedrandom';

function hard() {
  const now = new Date();
  const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
  const seed = today.getTime().toString();

  //    
  const rng = seedrandom(seed);
  
  // 1 23742  
  const randomNumber = Math.floor(rng() * 26096) + 1;
  
  return randomNumber;
}

